html {
  --default-font-family: 'Roboto', sans-serif;
}

@font-face {
  font-family: 'Azhagi';
  font-display: swap;
  src: url('assets/fonts/TSCu_SaiIndira.ttf') format('truetype');
}

body {
  margin: 0;
  font-family: var(--app-font-family, var(--default-font-family));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
  display: none;
}

html,
body,
#root,
#root > div {
  height: 100%;
}

* .is-app-color {
  color: var(--app-color) !important;
}

